.principal{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    padding: 30px;
    box-sizing: border-box;
    justify-content: space-evenly;
}

@media screen and (min-width: 450px) {
    .principal{
        display: grid;
        grid-template-columns: 50% 50%;
    }

    .principal .titulo{
        margin: auto 0;
    }

    .principal>.login{
        width: 70%;
        margin: auto auto;
        height: min-content;
    }
}

.titulo h1{
    font-size: 48px;
    color: var(--azul-principal);
}

.login{
    border-radius: var(--curvatura);
    margin: 0 auto;
    width: 70%;
    padding: 30px;
    -webkit-box-shadow: 8px 10px 44px -6px rgba(0,0,0,0.75);
    -moz-box-shadow: 8px 10px 44px -6px rgba(0,0,0,0.75);
    box-shadow: 8px 10px 44px -6px rgba(0,0,0,0.75);
}

.contenedor{
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding-bottom: 15px;
    border-bottom: 4px solid var(--gris);
}

.contenedor>input{
    padding-left: 5px;
    height: 30px;
    border-radius: var(--curvatura);
    border: var(--borde-gris);
}

.contenedor>button{
    height: 30px;
    width: 50%;
    margin: 0 auto;
    border-radius: var(--curvatura);
    cursor: pointer;
    background-color: var(--azul-principal);
    border: none;
    color: white;
}

.login .link{
    display: block;
    margin-top: 10px;
    color: var(--azul-principal);
    text-decoration: none;
}
